							{{ getText('projects') }}
						</router-link>
						<router-link :to="this.getRouteUrl('contacts')"
						             class="text-white md:hover:text-gray-300 px-2 md:px-3 py-4 md:py-2 flex items-center text-sm uppercase font-bold">
