</template>
<script>
	import LanguageSelector from './LanguageSelector';
	import {mapGetters} from 'vuex'

	export default {
		data() {
			return {
				showMenu: false,
				// Hidden until all other texts are ready
				showLangDropdown: true,
			};
		},
		components: {
			LanguageSelector: LanguageSelector,
		},
		methods: {
			toggleNavbar: function () {
				this.showMenu = !this.showMenu;
				document.getElementById('nav-icon1').classList.toggle('open');
			},
		},
		computed: {
			...mapGetters(['getText', 'getRouteUrl'])
		},
		watch: {
			// Listen to a route change and close the menu
			$route() {
				this.showMenu = false;
				document.getElementById('nav-icon1').classList.remove('open');
			}
		}
	};
</script>

